import React  from 'react';
import { ContWrap, PcBlock} from "../common/common.style";
import { MainWrap, MainHeaderStyle, ContLetter, MemorialWrap, MemorialSlide, ServiceSec } from './main.style';
import { MainSwiper } from './MainSwiper';
import { MainHeader } from './MainHeader';
import { Service } from './Service';
import { Header } from '../CommonLayout/Header';
import { MainContWidth } from './main.style';
// import { SwiperJq } from './SwiperJq';

export const Main = () => {
    return (
        <>
            <Header />
            <MainWrap>
                <ContWrap>
                    <MainHeaderStyle>
                        <MainContWidth>
                            <MainHeader />
                        </MainContWidth>
                    </MainHeaderStyle>
                    <section>
                        <ContLetter>With Remembered</ContLetter>
                        <MemorialWrap>  
                            <MainContWidth>
                                <h2 className="title"></h2>
                                <div className='cont_top'>
                                    <p>      <PcBlock>    .</PcBlock></p>
                                    <button className='btn'></button>
                                </div>
                            </MainContWidth>
                            <MemorialSlide>
                                <MainSwiper type="memorial"/>
                               {/* <SwiperJq /> */}
                            </MemorialSlide>
                        </MemorialWrap>
                    </section>
                    <ServiceSec>
                        <MainContWidth>
                            <Service />
                        </MainContWidth>
                    </ServiceSec>
                </ContWrap>
            </MainWrap>
        </>
    );
}