import { MbBlock } from "../common/common.style"
import { PortraitStyle, ViewTopTxt } from "./memorial.style"

  export const ViewTopArea = ({type, memorialImg}) => {
    return (
      <>
        {type === "memorial" && (
            <PortraitStyle>
               <img src={memorialImg} alt="  " />
            </PortraitStyle>
        )}
      {type !== "memorial" && (
        <ViewTopTxt>
          <h2> <MbBlock />   </h2>
          <span className="desc">     .</span>
        </ViewTopTxt>
      )}
    </>
  )
}