import { ServiceStyle } from "./main.style"
import IcoSv1 from '../images/main/ico_service1.png';
import IcoSv2 from '../images/main/ico_service2.png';
import IcoSv3 from '../images/main/ico_service3.png';
import IcoSv4 from '../images/main/ico_service4.png';

const ServiceItem = ({imgSrc, imgAlt, title, text, classname}) =>{
  return (
    <li className={classname}>
      <a href="#">
        <i><img src={imgSrc} alt={imgAlt} /></i>
        <strong>{title}</strong>
        <p>{text}</p>
      </a>
    </li>
  )
}
export const Service = () => {
    return (
       <ServiceStyle>
            <ServiceItem 
              imgSrc={IcoSv1}
              imgAlt=""
              title=""
              text="       ."
              classname="sv1"
            />
            <ServiceItem 
              imgSrc={IcoSv2}
              imgAlt=""
              title=""
              text=" ,        ."
              classname="sv2"
            />
            <ServiceItem 
              imgSrc={IcoSv3}
              imgAlt=" "
              title=""
              text="      ."
              classname="sv3"
            />
            <ServiceItem 
              imgSrc={IcoSv4}
              imgAlt="FAQ "
              title="FAQ"
              text="  ,       ."
              classname="sv4"
            />
       </ServiceStyle>
    )
}